$(document).ready(function() {

    //Animate CSS + WayPoints javaScript
    //Example: $(".element").animated("zoomInUp");
    $.fn.animated = function(inEffect) {
        $(this).css("opacity", "0").addClass("animated").waypoint(function(dir) {
            if (dir === "down") {
                $(this).addClass(inEffect).css("opacity", "1");
            };
        }, {
            offset: "85%"
        });
    };

    $(window).load(function() {
        // Header Animations Start
        $('.header img').animated('zoomIn');
        $('.header h1').animated('fadeInDownBig');
        $('.header p').animated('fadeInLeftBig');
        $('.header span').animated('fadeInRightBig');
        $('.header .social').animated('fadeInUpBig');
        // Header Animations End

        // Hide about-text element on page load
        $('.about-text').addClass('animated');
        $('.about-text').css('opacity', '0');

        // Hide service-item element on page load
        $('.service-item').addClass('animated');
        $('.service-item').css('opacity', '0');

        // Hide portfolio-item element on page load
        $('.portfolio-item').addClass('animated');
        $('.portfolio-item').css('opacity', '0');

    });

    // Animations for all section titles
    $('.section-title').animated('fadeInDown');
    $('.animate-btn').animated('fadeInUp');

    // Services Items Animations Start
    $('.services').waypoint(function() {
        $('.service-item').each(function(index) {
            var item = $(this);
            setInterval(function() {
                item.addClass('zoomIn').css('opacity', '1');
            }, 400*index);
        });
    }, {offset:'70%'});
    // Services Items Animations End

    // Latest Works Animations End
    $('.work-info-left').animated('zoomIn');
    $('.work-img-right').animated('fadeInUp');
    $('.devider').animated('fadeInUp');
    $('.work-info-right').animated('zoomIn');
    $('.work-img-left').animated('fadeInUp');
    // Latest Works Animations End

    // Call To Action Animations Start
    $('.action-text').animated('zoomIn');
    // Call To Action Animations End

    // Profile Animations Start
    $('.about').waypoint(function() {
        $('.about-text').each(function(index) {
            var item = $(this);
            setInterval(function() {
                item.addClass('zoomIn').css('opacity', '1');
            }, 400*index);
        });
    }, {offset:'70%'});
    // Profile Animations End

    // Skills-item Animations Start
     $('.skills-item').animated('zoomIn');
     // Skills-item Animations End

    // Portfolio Items Animations Start
    $('.portfolio').waypoint(function() {
        $('.portfolio-item').each(function(index) {
            var item = $(this);
            setInterval(function() {
                item.addClass('zoomIn').css('opacity', '1');
            }, 400*index);
        });
    }, {offset:'60%'});

    $('.project-action').animated('fadeInUp');
    // Portfolio Items Animations End

    // Contacts Animations End
    $('.contact-left').animated('zoomIn');
    $('.contact-right').animated('zoomIn');
    // Contacts Animations End

    // Refresh scrollSpy function
    function scrollSpyRefresh(){
        setTimeout(function(){
            $('body').scrollspy('refresh');
        },1000);
    }

    // Refresh waypoints function
    function waypointsRefresh(){
        setTimeout(function(){
            $.waypoints('refresh');
        },1000);
    }

    $('.skills').waypoint(function() {
        $('.chart').each(function() {
        $(this).easyPieChart({
                size: 130,
                animate: 2000,
                lineCap:'butt',
                scaleColor: false,
                trackColor: 'transparent',
                barColor: '#00CCCB',
                lineWidth: 5,
                easing:'easeOutQuad'
            });
        });
    }, {offset:'80%'});

    $("[data-toggle='tooltip']").tooltip({container: 'body'});

});
$(document).ready(function(){

    $('#contact-form .form-control').each(function() {

        if ($.trim($(this).val()) === '') {
            $(this).removeClass('input-filled');
        } else {
            $(this).addClass('input-filled');
        }
    });

    $('#contact-form .form-control').on('blur', function() {

        if ($.trim($(this).val()) === '') {
            $(this).removeClass('input-filled');
        } else {
            $(this).addClass('input-filled');
        }
    });

    // Form Validation

    $('#contact-form .form-control').on('focus', function() {
        $(this).parent('.controls').find('.error-message').fadeOut(300);
    });

    $('#contact-form').submit(function() {

        if ($('#contact-form').hasClass('clicked')) {
            return false;
        }

        $('#contact-form').addClass('clicked');

        var buttonCopy = $('#contact-form button').html(),
            errorMessage = $('#contact-form button').data('error-message'),
            sendingMessage = $('#contact-form button').data('sending-message'),
            okMessage = $('#contact-form button').data('ok-message'),
            hasError = false;

        $('#contact-form .error-message, #contact-form .contact-form-message').remove();

        $('.requiredField').each(function() {
            if ($.trim($(this).val()) === '') {

                var errorText = $(this).data('error-empty');
                $(this).next('label').append('<span class="error-message" style="display:none;">'+errorText+'.</span>').find('.error-message').fadeIn('fast');
                hasError = true;
            } else if ($(this).is("input[type='email']") || $(this).attr('name') === 'email') {

                var emailReg = /^([\w-\.]+@([\w-]+\.)+[\w-]{2,6})?$/;
                if (!emailReg.test($.trim($(this).val()))) {
                    var invalidEmail = $(this).data('error-invalid');
                    $(this).next('label').append('<span class="error-message" style="display:none;">'+invalidEmail+'.</span>').find('.error-message').fadeIn('fast');
                    hasError = true;
                }
            }
        });

        if (hasError) {
            $('#contact-form').append('<p class="contact-form-message">'+errorMessage+'</p>');
            $('#contact-form').removeClass('clicked');
        }
        else {
            var formInput = $(this).serialize();
            $.post($(this).attr('action'), formInput);
            $('#contact-form').append('<p class="contact-form-message">'+okMessage+'</p>');
            $('#contact-form').removeClass('clicked');
            $('#contact-form')[0].reset();
            $('#contact-form .form-control').removeClass('input-filled');
        }

        return false;
    });

});
$(document).ready(function() {

    // Navigation Functions
    if ($(window).scrollTop()< 10) {
        $('#main-nav').removeClass('scrolled');
    }
    else{
        $('#main-nav').addClass('scrolled');
    }

    $(window).scroll(function() {
        if ($(window).scrollTop()< 10) {
            $('#navigation').removeClass('scrolled');
        }
        else{
            $('#navigation').addClass('scrolled');
        }
    });

    $('a.scrollto').click(function(e) {
        e.preventDefault();
        var target = $(this).attr('href');
        $('html, body').stop().animate({scrollTop: $(target).offset().top}, 1600, 'easeInOutExpo',
            function(){window.location.hash = target;});

        if ($('.navbar-collapse').hasClass('in')){
            $('.navbar-collapse').removeClass('in').addClass('collapse');
        }
    });

});
$(window).on('load', function () {
    var $preloader = $('#page-preloader'),
        $spinner   = $preloader.find('.fa');

    $spinner.fadeOut();
    $preloader.delay(350).fadeOut('slow');
});
